

.products_section{
    max-width: 1200px;
    height: 1200px;
    margin: 0 auto;
  }
  
  .products_section h2{
    text-align: center;
  }
  
  .products{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  } 
  
  .product_title{
    font-size: 20px;
    font-weight: bold;
  
  }
  
  .price{
    color: #9D9D9D;
    font-weight: bold;
  }
  
  
  /* Поиск */
  
  .search{
    background-color: #3547e9;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
    margin-right: 25px;    
  }
  
  .search h2 {
    justify-self: start;
    font-size: 32px;
    margin: 45px;
  }
  
  .search_block{
    justify-self: end;
    border: 1px solid #9f9f9f;
    border-radius: 10px;
    padding: 15px;
    width: 350px;
   
  }
  
  .search_block:hover{
    border: 1px solid #001aff;
  }
  
  .search_block img{
    width: 63px; 
    cursor: pointer;
   
  }
  
  .search_block img:hover {
    background-color: rgb(238, 226, 65);
    transition: 2s; 
  
  }
  
  .search_block input {  
    border: none;
    font-size: 16px;
    width: 200px;
    padding: 15px;
    height: 20px;
  }
  
  
  .search_block input:focus-visible {
    outline: none;
    border-bottom: 1px solid #001aff;
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  